
import { defineComponent } from 'vue';
import ReservationBasedStat from '@/models/ReservationBasedStat';

export default defineComponent({
  props: {
    auras: {
      type: Object,
      require: true,
    },

    globalAuraEffect: {
      type: Number,
      require: false,
      default: 0,
    },

    supportGemsStatic: {
      type: Object,
      require: true,
    },

    ascendancies: {
      type: Object,
      require: true,
    },

    passiveTree: {
      type: Object,
      require: true,
    },

    reservedValues: {
      type: Object,
      require: true,
    },
  },

  computed: {
    resStatArray(): string[] {
      const array: string[] = [];

      this.ascendancies.forEachReservationBasedStat((resStat: ReservationBasedStat) => {
        const statLine = resStat.getScaledStatLine(this.reservedValues);
        if (statLine) array.push(statLine);
      });

      return array;
    },
  },
});

