
import { defineComponent } from 'vue';

export default defineComponent({
  props: {
    imgLink: {
      type: String,
      require: true,
    },
  },

  computed: {
    img() {
      try {
        return require(`@/assets/${this.imgLink}`);
      } catch {
        return require('@/assets/img/tree/missing.png');
      }
    },
  }
});

