
import { defineComponent } from 'vue';

export default defineComponent({
  props: {
    node: {
      type: Object,
      require: true,
    },

    isNotable: {
      type: Boolean,
      require: true,
    },

    selected: {
      type: Boolean,
      require: true,
    }
  },

  computed: {
    active(): string {
      return this.node.active ? 'active' : '';
    },

    nodeBg() {
      try {
        return require(`@/assets/img/${this.node.icon}`);
      } catch {
        return require(`@/assets/img/Art/2DArt/SkillIcons/passives/missing.png`);
      }
    },
  },
});

