
import { defineComponent } from 'vue';

export default defineComponent({
  props: {
    auras: {
      type: Object,
      require: true,
    },

    globalAuraEffect: {
      type: Number,
      require: false,
      default: 0,
    },

    supportGemsStatic: {
      type: Object,
      require: true,
    },

    ascendancies: {
      type: Object,
      require: true,
    },

    passiveTree: {
      type: Object,
      require: true,
    },
  },

  data() {
    return {
      // afsAEArray: [] as number[],
    };
  },

  computed: {
    miscStats(): string[] {
      const array: string[] = [];

      this.ascendancies.forEachMiscStat((stat: string) => {
        array.push(stat);
      });

      return array;
    },
  },
});

